/* Dropdown control */
.selectBox-dropdown { padding-right: 30px; height: 37px; line-height: 37px; width: 100%; font-size: 16px; cursor: pointer; color: #333; position: relative; }

.selectBox-dropdown:focus, .selectBox-dropdown:focus .selectBox-arrow { border-color: #444; }

.selectBox-dropdown.selectBox-menuShowing { -moz-border-radius-bottomleft: 0; -moz-border-radius-bottomright: 0; -webkit-border-bottom-left-radius: 0; -webkit-border-bottom-right-radius: 0; border-bottom-left-radius: 0; border-bottom-right-radius: 0; }

.selectBox-dropdown .selectBox-label { width: 100%; padding: .2em .3em; display: inline-block; white-space: nowrap; overflow: hidden; }

.selectBox-dropdown .selectBox-arrow { position: absolute; top: 20px; right: 30px; background: url('../img/icons-s8c8cfbcf62.png') 0 0 no-repeat; width: 12px; height: 7px; display: inline-block; }

/* Dropdown menu */
.selectBox-dropdown-menu { position: absolute; z-index: 99999; max-height: 200px; border: solid 1px #BBB; /* should be the same border width as .selectBox-dropdown */ background: #FFF; -moz-box-shadow: 0 6px 12px rgba(0, 0, 0, 0.6); -webkit-box-shadow: 0 6px 12px rgba(0, 0, 0, 0.6); box-shadow: 0 6px 12px rgba(0, 0, 0, 0.6); overflow: auto; }

/* Inline control */
.selectBox-inline { width: 250px; outline: none; border: solid 1px #BBB; background: #FFF; display: inline-block; -webkit-border-radius: 4px; -moz-border-radius: 4px; border-radius: 4px; overflow: auto; }

.selectBox-inline:focus { border-color: #666; }

/* Options */
.selectBox-options, .selectBox-options LI, .selectBox-options LI A { list-style: none; display: block; cursor: default; padding: 0; margin: 0; }

.selectBox-options LI A { line-height: 1.5; padding: 0 .5em; white-space: nowrap; overflow: hidden; background: 6px center no-repeat; }

.selectBox-options LI.selectBox-hover A { background-color: #EEE; }

.selectBox-options LI.selectBox-disabled A { color: #888; background-color: transparent; }

.selectBox-options LI.selectBox-selected A { background-color: #C8DEF4; }

.selectBox-options .selectBox-optgroup { color: #666; background: #EEE; font-weight: bold; line-height: 1.5; padding: 0 .3em; white-space: nowrap; }

/* Disabled state */
.selectBox.selectBox-disabled { color: #888 !important; }

.selectBox-dropdown.selectBox-disabled .selectBox-arrow { opacity: .5; filter: alpha(opacity=50); border-color: #666; }

.selectBox-inline.selectBox-disabled { color: #888 !important; }

.selectBox-inline.selectBox-disabled .selectBox-options A { background-color: transparent !important; }

